input[type="submit"]{
	-webkit-appearance: none;
	border-radius: 0;
	&.button {
		border-radius: 5px;
	}

}
body{
	position: relative;
	min-width: 320px;
}
.goods-amount{
	float: left;
	margin-top: 20px;
	font-size: 14px;
	input{
		font-size: 14px;
		vertical-align: top;
		border: none;
		background-color: $BorderColor;
		width: 25px;
		height: 20px;
		line-height: 20px;
		vertical-align: middle;
		text-align: center;
	}
	.up i:before{
		margin-top: -6px;
	}
	.down i:before{
		margin-top: -1px;
	}
	.down, .up{
		cursor: pointer;
		color: $BorderColor;
		background-color: $DarkBG;
		display: inline-block;
		vertical-align: middle;
		width: 20px;
		height: 20px;
		line-height: 18px;
		text-align: center;
		vertical-align: middle;
		position: relative;
		@extend .noselect;
		i:before{
			position: absolute;
			font-size: 6px;
			left: 50%;
			top: 50%;
			margin-left: -6px;
		}
	}
}
.checkbox-view{
	float: right;
	a{
		margin-left: 10px;
		line-height: 24px;
		vertical-align: bottom;
		font-size: 18px;
	}
	.checkbox{
		vertical-align: middle;
		display: inline-block;
		position: relative;
		width: 25px;
		height: 25px;
		margin-left: 20px;
	}
	p{
		vertical-align: top;
		display: inline-block;
		line-height: 24px;
	}
}
.affix{
	top: 0;
	position: fixed !important;
	&-top{
		position: relative;
	}
}
.retings{
	float: right;
	@extend .clearfix-extendet;
	li{
		float: left;
		position: relative;
		display: block;
		width: 21px;
		height: 21px;
		input[type="checkbox"] {
			width: 21px;
			height: 21px;
			opacity: 0;
			position: relative;
			z-index: 2;
			margin-left: 5px;
			cursor: pointer;
			+ label{
				z-index: 1;
				left: 2px;
				top: 5px;
				position: absolute;
				@include sprite(star);
				font-size: 8px;
				&:not(:first-child){
					margin-left: 5px;
				}
			}
		}
	}
	.retings-user{
		width: auto;
		position: relative;
		margin: 7px 10px 0 25px;
		font-size: 14px;
		i{
			top: -2px;
			left: -25px;
			position: absolute;
			display: inline-block;
		}
	}
}
.select-box-container{
	background-color: $MainBG;
	width: 100%;
	padding: 15px;
}
.select-box-btn {
	margin-top: 50px;
}
.select-box-inline {
	padding: 10px 20px;
	label {
		width: 100%;
		font-size: 14px;
		font-weight: 600;
		margin-bottom: 5px;
		display: block;
	}
	input {
		left: 0;
		border-radius: 5px;
		background-color: #fff;
		padding: 5px 10px 5px 10px;
		border: 1px solid #D5D5D5;
	}
	input, .select {
		width: 100%;
		white-space: nowrap;
	}
}
.select-box-wrapper{
	vertical-align: middle;
	height: 43px;
	.select-box{
		margin: 10px 7px 0 7px;
		display: inline-block;
		label{
			font-size: 14px;
			margin-right: 5px;
			line-height: 26px;
			vertical-align: -3px;
		}
	}
	.btn-group{
		button:last-child{
			margin-left: 10px;
		}
	}
}
.hidden{
	display: none;
}
.pull-right{
	float: right;
}
.select-light {
	float: left;
	.s-hidden {
		visibility:hidden;
		padding-right:10px;
		position: absolute;
		height: 0;
	}
	.select {
		cursor: pointer;
		display: inline-block;
		position: relative;
		font-weight: 300;
		border-radius: 5px;
		color:black;
		vertical-align: middle;
		line-height: 14px;
		font-size: 14px;
		font-weight: 300;
	}
	select:focus{
		.styledSelect:after {
			border-color:  transparent transparent $redColorHover transparent;
		}
	}
	.styledSelect {
		border-radius: 5px;
		position: relative;
		color: #fff;
		&:hover{
			&:after {
				border-color: $redColorHover transparent transparent transparent;
			}
			color: $redColorHover;
		}
		&:active{
			&:after {
				border-color: $redColorActive transparent transparent transparent;
			}
			color: $redColorActive;
		}
		&:after {
			content:"";
			width:0;
			height:0;
			border: 5px solid transparent;
			border-color: #fff transparent transparent transparent;
			position:absolute;
			top: 5px;
			right: -15px;
		}
		&:active, &.active {
			&:after {
				top: 0px;
				border-color: transparent transparent $redColorHover transparent;
			}
		}
	}
	.options {
		display:none;
		position:absolute;
		top:20px;
		right:0;
		left:-5px;
		z-index:999;
		margin:0 0;
		padding:0 0;
		list-style:none;
		background-color: $DarkBG;
		color: #fff;
		-webkit-box-shadow:0 1px 2px rgba(0, 0, 0, 0.2);
		-moz-box-shadow:0 1px 2px rgba(0, 0, 0, 0.2);
		box-shadow:0 1px 2px rgba(0, 0, 0, 0.2);
		li {
			padding: 5px 6px;
			margin: 0 0;
			&:hover {
				color: $redColor;
			}
		}
	}
	.hide-mobile{
		display: inline-block;
	}
}
.select-box{
	.input-file{
		border: 1px solid $BorderColor;
		border-radius: 5px;
		font-weight: 300;
		font-size: 12px;
		height: 26px;
		cursor: pointer;
		display: inline-block;
		color: black;
	}
	.s-hidden {
		visibility:hidden;
		padding-right:10px;
	}
	.select {
		cursor: pointer;
		display: inline-block;
		position: relative;
		font-size: 12px;
		font-weight: 300;
		border-radius: 5px;
		color:black;
		border: 1px solid $BorderColor;
		height: 26px;
	}
	.styledSelect {
		position:absolute;
		font-weight: 300 !important;
		top:0;
		right:0;
		bottom:0;
		left:0;
		border-radius: 5px;
		background-color: $InputBG;
		padding: 5px 10px 0 10px;
		font-weight:bold;
	}
	.styledSelect:after {
		content:"";
		width:0;
		height:0;
		border: 5px solid transparent;
		border-color: $DarkBG transparent transparent transparent;
		position:absolute;
		top: 9px;
		right: 5px;
	}
	.styledSelect:active, .styledSelect.active {
		background-color:#eee;
	}
	.options {
		display:none;
		position:absolute;
		top:100%;
		right:0;
		left:0;
		z-index:999;
		margin:0 0;
		padding:0 0;
		list-style:none;
		border:1px solid #ccc;
		background-color:white;
		-webkit-box-shadow:0 1px 2px rgba(0, 0, 0, 0.2);
		-moz-box-shadow:0 1px 2px rgba(0, 0, 0, 0.2);
		box-shadow:0 1px 2px rgba(0, 0, 0, 0.2);
		li {
			padding: 5px 6px;
			margin: 0 0;
			&:hover {
				background-color:#39f;
				color:white;
			}
		}
	}
	.hide-mobile{
		display: inline-block;
	}
}
.checkbox{
	input[type="checkbox"],
	input[type="radio"]{
		position: absolute;
		display: none;
	}
	input[type="checkbox"] + label{
		position: relative;
		padding-left: 24px;
		cursor: pointer;
		&:before {
			font-size: .6em;
		}
	}
	input[type="checkbox"] + label,
	input[type="radio"] + label{
		&:before{
			content: '';
			@include sprite(check);
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	input[type="checkbox"]:checked + label,
	input[type="radio"]:checked + label{
		&:before{
			content: '';
			@include sprite(checked);
			position: absolute;
			top: 0;
			left: 0;
		}
	}
}
.pagination{
	font-weight: 300;
	margin-top: 35px;
	text-align: center;
	vertical-align: middle;
	line-height: 25px;
	li{
		display: inline-block;
		text-align: center;
		margin: 0 5px;
		&:not(.pagination_btn),
		&:not(.pagination_btn) a{
			width: 25px;
			height: 25px;
			display: inline-block;
			&.active,
			&:hover{
				border-radius: 50%;
				width: 25px;
				height: 25px;
				display: inline-block;
			}
		}
		&.active{
			color: #fff;
		}
		a{
			&:hover{
				color: #fff;
				text-decoration: none;
			}
		}
	}
	.pagination_btn{
		margin: 0 10px;
		a{
			text-align: center;
			@extend .default-boreder;
			padding: 0 15px;
			border-radius: 15px;
			display: inline-block;
		}
		&.disable{
			padding: 0 15px;
			border-radius: 15px;
			background-color: $BorderColor;
			color: $MainBG;
		}
	}
}
.clearfix:after {
	visibility: hidden;
	display: block;
	font-size: 0;
	content: " ";
	clear: both;
	height: 0;
}
.clearfix-extendet{
	&:after,
	&:before{
		display: table;
		clear: both;
		content: '';
	}
}
.clearfix { display: inline-block; }
// start commented backslash hack \
* html .clearfix { height: 1%; }
.clearfix { display: block; }
// close commented backslash hack
.button{
	font-size: 18px;
	padding: 9px 0 10px;
	text-align: center;
	border-radius: 5px;
	&.defaultBtn{
		padding-left: 15px;
		padding-right: 15px;
		border: none;
		color: #fff;
		cursor: pointer;
	}
	&:hover{
		text-decoration: none;
	}
	&.CancelBtn{
		background-color: $DarkBG;
		color: $MainBG;
		&:hover{
			background-color: darken($DarkBG, 20%);
		}
	}
}

.shipment-button{
  padding-left: 15px;
  padding-right: 15px;
}

.auxiliary-buttons {
	width: 50%;
	float: left;
	margin-top: 15px;
	font-size: 12px;
	a{
		cursor: pointer;
		i{
			font-size: 6px;
			line-height: 11px;
			top: 0;
			position: absolute;
		}
	}
	span{
		padding-bottom: 15px;
		display: inline-block;
	}
	a,span {
		line-height: 12px;
		font-size: 12px;
		position: relative;
		padding-left: 10px;
		i{
			top: 1px;
			left: -5px;
			position: absolute;
			&.libra-icon{
				top: 3px;
			}
			&.order-icon{
				top: 0px;
				font-size: 7px;
			}
		}
	}
}
i{
	display: block;
	&:before{
		content: '';
	}
}
.border-icons {
  border-left: solid 1px $BorderColor;
  padding-left: 25px;
}
.border--left-side {
  border-bottom: solid 1px $BorderColor;
}
.default-boreder{
	border: solid 1px $BorderColor;
}
.default-border-top{
	border-top: solid 1px $BorderColor;
}
.default-border-bottom{
	border-bottom: solid 1px $BorderColor;
}
.default-dashed-border-bottom{
	border-bottom: dashed 1px $BorderColor;
}
.default-dashed-top-bottom{
	border-top: dashed 1px $BorderColor;
}
.default-border-right{
	border-right: solid 1px $BorderColor;
}
.default-border--left{
	border-left: solid 1px $BorderColor;
}
.center.aboslute{
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
.text-center{
	text-align: center;
}
.text-right {
	text-align: right;
}
.section-right--textarea{
	margin-top: 25px;
	box-shadow: none;
	background-color: #F5F5F5;
	border-radius: 5px;
	height: 100px;
	border: 1px solid #D5D5D5;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}
.table-contnent {
	.rouble-icon {
		vertical-align: -.1em;
		margin-right: .6em;
		font-size: .4em;
	}
}

.table-buttons {
	.print-icon {
		font-size: 1em;
		vertical-align: -.1em;
		margin-right: .6em;
	}
	.disk-icon{
		vertical-align: -.1em;
		margin-right: 1em;
		font-size: .5em;
	}
}
.top-button {
	margin-top: 15px;
}
.bottom-button{
	margin-bottom: 20px;
}
.table-content-operation {
	margin-top: 15px;
	margin-bottom: 15px;
}
.select-col-wrapper {
	margin-bottom: 20px;
}
.select-col {
	@extend .clearfix-extendet;
	height: 100%;
	.button {
		margin-top: 11px;
	}
	.select-box {
		width: 100%;
		padding-right: 25px;
		label {
			width: 50%;
		}
		.select {
			float: right;
			width: 50%;
		}
	}
}
.table-btn {
	&.button {
		font-size: 1em;
		padding: 5px 10px 7px;
	}
}