.main-cart {
  .checkbox-view p{
    line-height: 18px;
    vertical-align: top;
  }
  .select-box {
    .select {
      width: 100%;
      height: 50px;
      font-size: 14px;
      line-height: 38px;
      vertical-align: middle;
    }
    .styledSelect::after {
      top: 22px;
      right: 15px;
    }
  }
  .nav-tabs {
    display: inline-block;
    border: 0;
  }
  .closeRed-icon {
    font-size: 8px;
    float: right;
  }
  .auxiliary-buttons {
    float: left;
    margin-top: 0;
    span {
      margin-left: 0;
    }
  }
  .block-goods--wrapp {
    margin-top: -1px;
    @extend .default-border-top;
    .goods-amount {
      .title {
        line-height: 19px;
        margin-right: 10px;
      }
      i {
        font-size: 6px;
      }
      margin-top: 0;
    }
  }
  .orderLine {
    height: 75px;
    padding: 0 15px;
    line-height: 75px;
    background-color: $MainBG;
    @extend .default-boreder;
    #sum {
      float: left;
    }
    .label {
      margin-right: 12px;
      font-size: 14px;
    }
    .price,
    .price span {
      font-weight: bold;
      font-size: 16px;
    }
    #countItems,
    #allDevilerySum,
    #countOrderItems {
      margin-right: 24px;
    }
  }
  #coupon {
    text-align: right;
    float: right;
    width: 50%;
    input {
      margin-top: -3px;
      vertical-align: middle;
      display: inline-block;
      height: 48px;
      border: 0;
    }
    .couponField {
      width: 300px;
      border: 1px solid #e7e8ea;
      padding-left: 8px;
      color: #888888;
    }
    .couponActivate {
      border: 1px solid #2d2d2d;
      background-color: #2d2d2d;
      text-align: center;
      padding: 0 24px;
      cursor: pointer;
      color: #ffffff;
      &:hover{
        background-color: lighten(#2d2d2d, 25%);
      }
    }
  }
}

#order {
  margin-top: 24px;
  border-collapse: collapse;
  .title {
    font-weight: bold;
    text-transform: uppercase;
    border: 1px solid #eeeeee;
    text-overflow: ellipsis;
    text-align: center;
    border-bottom: 0px;
    line-height: 80px;
    overflow: hidden;
    font-size: 16px;
    display: block;
    height: 80px;
  }
  textarea {
    padding-top: 12px;
    max-width: 100%;
    outline: none;
    resize: none;
  }
}

.personSelect,
.orderProps {
  border-collapse: collapse;
  border: 1px solid #eeeeee;
  table-layout: fixed;
  position: relative;
  border-bottom: 0px;
  width: 100%;
  td {
    border-top: 1px solid #eeeeee;
    position: relative;
    padding: 24px 0;
    &:first-child {
      vertical-align: middle;
      text-align: center;
      width: 250px;
      span {
        font-weight: bold;
        text-transform: uppercase;
        text-align: center;
        margin-top: 12px;
        font-size: 14px;
        display: block;
        color: #888888;
      }
    }
    &:last-child {
      padding-right: 24px;
    }
  }
  label {
    margin-bottom: 6px;
    padding-top: 2px;
    display: block;
    font-size: 12px;
    color: #aaaaaa;
  }
  select {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    border: 1px solid #dddddd;
    background: #ffffff;
    padding-left: 12px;
    font-size: 14px;
    display: block;
    height: 50px;
    width: 80%;
    clear: both;
  }
}
.order.button{
  position: relative;
  display: inline-block;
  padding: 0 45px;
  line-height: 45px;
  margin-right: 25px;
  i:before{
    left: 10px;
    top: 7px;
    position: absolute;
    font-size: 12px;
  }
}
.orderProps {
  display: none;
  &.active {
    display: table;
  }
  .label {
    margin-bottom: 6px;
    margin-right: 24px;
    float: left;
  }
  input {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    border: 1px solid #dddddd;
    background: #ffffff;
    padding-left: 12px;
    font-size: 14px;
    display: block;
    height: 50px;
    width: 100%;
    clear: both;
  }
  textarea {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-size: 14px;
    display: block;
    width: 100%;
    height: 100px;
    margin-top: 6px;
    background: #ffffff;
    border: 1px solid #ddd;
    padding-top: 4px;
    padding-left: 12px;
    clear: both;
  }
}

.userProp {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    position: relative;
    margin: 24px 0 0 0;
    overflow: hidden;
    list-style: none;
    display: block;
    padding: 0;
    // &:first-child {
    //   margin: 0;
    // }
  }
  &--fourth{
    li{
      width: 33.3332%;
      clear: none;
      float: left;
    }
    &:first-child{
      li{
        margin-right: 1%;
      }
    }
  }
  &--half{
    li{
      width: 49%;
      clear: none;
      float: left;
    }
    &:first-child{
      li{
        margin-right: 1%;
      }
    }
  }
}
.block-goods--table{
  overflow: auto;
  width: 100%;
}
.goodCartList{
  @extend .default-border-right;
  width: 100%;
  th{
    background-color: $BorderColor;
    padding: 10px 0;
    font-size: 14px;
    &.named{
      width: 20%;
    }
  }
  .closeRed-icon{
    float: none;
  }
  td{
    @extend .default-border-bottom;
    text-align: center;
    line-height: 24px;
    padding: 15px 5px;
    vertical-align: middle;
  }
  .goods-amount{
    min-width: 75px;
    margin-top: 0;
    float: none;
  }
  .auxiliary-buttons{
    width: 100%;
    float: none;
    min-width: 97px;
    span{
      padding-bottom: 0;
      font-size: 16px;
    }
  }
  img{
    max-width: 100px;
  }
}
.input-width--checbox{
  .userProp{
    display: inline-block;
    width: 80%;
    clear: none;
  }
  .checkbox{
    display: inline-block;
  }
}