.col {
	float: left;
}
.col-2 {
	width: 16.66666667%;
}
.col-4 {
	width: 25%;
}
.col-6 {
	width: 50%;
}
.col-10 {
	width: 83.33333333%;
}
.container{
	max-width: 1280px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;
	.with--aside{
		margin-left: 290px;
		position: relative;
		&:before,
		&:after{
			content: '';
			display: table;
			clear: both;
		}
		.block-goods--wrapp {
			width: 33.3%;
			&.block-goods-list--wrapp{
				width: 100%;
			}
		}
	}
	.no--aside{
		padding-bottom: 50px;
		.aside.aside-left{
			float: none;
			margin-left: 0;
			position: relative;
		}
		.section-content{
			float: none;
		}
		.block-goods .block-goods--wrapp {
			width: 33.3%;
		}
	}
}
.main.main-good{
	.no--aside{
		.left-side--catalog_content{
			display: none !important;
		}
		.toggle-link.toggle-link--active + .left-side--catalog_content{
			left: 0;
			display: block !important;
		}
	}
}
.aside-left{
	float: left;
	width: 270px;
	margin-left: -290px;
}
.section-right{
	float: right;
	width: 270px;
}
.block-goods--wrapp.block-goods-list--wrapp{
	width: 100%;
}

.block-goods--wrapp,
.block-brands--wrapp{
	width: 20%;
	height: 215px;
}
.block-goods_img {
	height: 100%;
	overflow: hidden;
}
.footer{
	.nav-footer{
		width: 65%;
		float: left;
		.nav-footer-col{
			width: 33.3331%;
			float: left;
		}
	}
	.contacts-block{
		width: 36%;
		float: left;
		.col{
			width: 50%;
			float: left;
		}
	}
}
.col-fix-left-panel{
	width: 50%;
	float: left;
}

.col-fix-right-panel{
	width: 50%;
	float: left;
}

////Login
.reg-col{
	width: 50%;
	float: left;
	&:nth-child(2n+1){
		padding-right: 10px;
	}
	&:nth-child(2n){
		padding-left: 10px;
	}
}

.tab-content > .tab-pane{
	width: 90%;
	.reg{
		width: 100%;
	}
	form{
		width: 50%;
		padding-right: 15px;
		float: left;
	}
	.social-sign{
		padding-left: 15px;
		width: 50%;
		float: left;
	}
}
@import '../../../bower_components/sass-mediaqueries/_media-queries.scss';

// 1250px
@include max-screen(1250px) {
	.footer{
		background-color: $DarkBG;
		.nav-footer{
			padding-left: 0;
			width: 100%;
			margin-left: 0;
			float: none;
		}
		.contacts-block{
			display: none;
		}
		.social-block{
			display: block;
			right: 25px;
		}
	}
}
// 1160px
@include max-screen(1200px) {
	.col-fix-right-panel .col .cabinet-hide {
		left: -600px;
		&.config-hide {
			left: 0;
		}
	}
	.block-goods--wrapp.block-goods-list--wrapp{
		padding-bottom: 0;
		.block-goods--inCart{
			background: $BorderColor;
			padding-bottom: 0;
			&:after,
			&:before{
				display: table;
				clear: both;
				content: '';
			}
			clear: both;
			float: none;
			width: auto;
			height: auto;
			border: 0;
			.price{
				float: left;
				width: 21%;
				margin-right: 25px;
				span{
					display: none;
				}
			}
			.button.defaultBtn{
				float: left;
				width: 200px;
				margin-right: 25px;
			}
			.auxiliary-buttons{
				width: 19%;
				margin-top: 0;
				margin-bottom: 15px;
				span{
					padding-bottom: 0;
				}
			}
		}
		.block-goods_title{
			width: 100%;
		}
		.block-goods_img{
			width: 25%;
		}
		.block-goods_description{
			width: 75%;
		}
	}
}
// 1124px
@include max-screen(1124px) {
	.order-table.order-table-right-section{
		margin-left: -315px;
	}
	.header{
		.nav-bar .nav-bar--item a span:not(.amount){
			display: none;
		}
		.logo{
			left: 15px;
		}
	}
	.main {
		.left-side--catalog ul.hide{
			left: 260px;
		}
	}
	.col-fix-left-panel{
		i {
			display: none;
		}
	}
}
// 1100px
@include max-screen(1100px) {
	.search-result .block-goods--wrapp{
		width: 33.332%;
	}
	.main.main-good .block-wrapp--good {
		margin-right: 0;
	}
	.desription--list,
	.section-right.section-right--good{
		display: none;
	}
	.main.main-good .block-review--desription {
		&.section-right--search {
			float: none;
			width: 100%;
			display: block;
			position: relative;
			padding: 0;
			a {
				display: block;
				padding: 0 15px;
				font-weight: 400;
				color: $redColor;
			}
			.select-box-container {
				margin: 0;
			}
		}
		.review-right--good{
			display: block;
			.section-right--good_title {
				margin-top: 0;
			}
			.auxiliary-buttons {
				height: auto;
			}
		}
	}
	.main.main-good .block-wrapp--good{
		margin-right: 0;
	}
	.col-filtr {
		.hidden {
			display: block;
		}
		.col {
			float: left;
			.toggle-link {
				color: #fff;
				&:hover, &:focus {
					text-decoration: none;
				}
			}
			.cabinet-hide a {
				color: #fff;
			}
			&.next-step {
				float: right;
			}
		}
	}
}
// 1066px
@include min-screen(1066px) {
	.main.main-good{
		.left-side--catalog_content{
			opacity: 0;
			position: absolute;
			transition: opacity .3s ease-in-out;
		}
		.toggle-link.toggle-link--active + .left-side--catalog_content{
			opacity: 1;
			position: absolute;
			background-color: #fff;
			padding-left: 25px;
			width: 345px;
			z-index: 10;
			left: -290px;
		}
	}
	.main .left-side--catalog_content{
		display: block !important;
	}
}
.select-list-box {
	li {
		padding-bottom: 5px;
		a {
			font-size: 16px;
		}
	}
}
