	.order-table.order-table-right-section{
		margin-left: 0;
		margin-top: 50px;
	}
	.section-right.section-right--search{
		margin-top: 50px;
	}
	.main{
		.left-side--catalog_list li:hover {
			ul.hide{
				display: none;
