.left-side_button--wrapp{
	margin-top: 30px;
	.button{
		padding: 5px 15px;
		margin-right: 5px;
		font-size: 16px;
		&.defaultBtn{
			float: left;
		}
		&.CancelBtn{
			float: right;
			margin-right: 0;
		}
	}
	@extend .clearfix-extendet;
}
.left-side--filter{
	font-size: 14px;
	font-weight: 300;
	.left-side_title{
		text-align: center;
	}
	.choes-prices--title{
		&.toggle-link{
			position: relative;
			display: inline-block;
		}
		&.toggle-link:before{
			content: '';
			position: absolute;
			@include sprite(arrowRight);
			right: -12px;
			top: 3px;
		}
		&.toggle-link--active:before{
			top: 6px;
			right: -14px;
			@include sprite(arrowDown);
		}
	}
	.toggle-content--hide{
		display: none;
	}
	.toggle-filter--list{
		text-decoration: underline;
		&:hover{
			text-decoration: none;
		}
	}
	li{
		cursor: pointer;
		line-height: 21px;
	}
	.checkbox{
		input[type="checkbox"] + label::before{
			top: 4px;
			font-size: 5px;
		}
		input[type="checkbox"]:checked + label::before{
			top: 4px;
		}
	}
}

//SLider Range
.choes-prices{
	font-size: 12px;
	font-weight: 300;
	input.amount-slider{
		width: 80px;
		border-radius: 3px;
		padding: 5px 10px;
		border: solid 1px $BorderColor;
		margin: 20px 4px 20px 10px;
	}
}
.ui-state-default{
	width: 24px;
	height: 24px;
	border-radius: 50%;
	top: 50%;
	margin-top: -12px;
	cursor: pointer;
	position: absolute;
	z-index: 2;
	box-shadow: 1px 1px 10px 0px rgba(0,0,0,.5);
	background-color: $MainBG;
	line-height: 22px;
	vertical-align: middle;
	text-align: center;
	&:focus{
		outline: none;
	}
	&:before{
		content: '';
		@include sprite(sliderCircle);
		line-height: 24px;
		vertical-align: middle;
	}
}
.ui-slider{
	border-radius: 5px;
	width: 100%;
	height: 10px;
	background-color: $DarkBG;
	box-shadow: inset 1px 1px 5px rgba(0,0,0,.5);
	position: relative;
}
.ui-slider-range{
	margin-left: 10px;
	box-shadow: inset 1px 1px 5px rgba(0,0,0,.5);
	position: absolute;
	border-radius: 5px;
	height: 10px;
}
.main-catlog{
	.block-goods{
		@extend .default-border-top;
	}
	.select-box{
		p{
			display: inline-block;
			margin-right: 20px;
			line-height: 24px;
		}
		.select{
			margin-right: 20px;
			vertical-align: middle;
		}
		float: left;
	}
}

.filterBtn {
	position: absolute;
	padding: 0 5px;
	z-index: 100;
	margin-top: -20px;
	margin-left: 180px;
	display: none;
}
