.jcarousel-wrapper{
	position: relative;
}


.jcarousel,
.connected-carousels .carousel-stage{
	position: relative;
	overflow: hidden;
	max-height: 330px;
	img{
		min-height: 330px;
		min-width: 100%;
		// width: 100%;
		height: auto;
	}
	>ul {
		width: 20000em;
		position: relative;
		list-style: none;
		margin: 0;
		padding: 0;
		li {
			float: left;
		}
	}
	&-control{
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		&:before{
			font-size: 1.5em;
			content: '';
		}
		&-prev{
			left: 25px;
			display: block;
		}
		&-next{
			right: 25px;
			display: block;
		}
	}
}

