//carousel-navigation
.connected-carousels{
	.carousel-stage{
		li{
			height: 400px;
			position: relative;
			a{
				cursor: zoom-in;
			}
			img{
				height: auto;
				width: 100%;
				max-width: 400px;
				@extend .center.aboslute;
			}
		}
	}
	.carousel-navigation{
		@extend .clearfix-extendet;
		text-align: center;
		li{
			cursor: pointer;
			display: inline-block;
			margin: 0 10px;
			padding-bottom: 15px;
			opacity: .6;
			&.active{
				border-bottom: solid 2px #000;
				opacity: 1;
			}
		}
		img{
			width: 50px;
			height: auto;
		}
	}
}
