
.fancybox-default-overlay{
  background: #fff !important;
  opacity: 1 !important;
}

.fancybox-custom{
  min-height: 100%;
  box-shadow: none !important;
  background: #fff !important;
  margin: 0px !important;
  padding: 0 45px !important;
  position: fixed !important;
  .fancybox-inner{
    min-height: 100%;
    min-width: 100%;
  }
  .fancybox-image{
    height: auto;
    max-height: 70%;
    width: auto !important;
    position: fixed;
    max-width: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  a{
    &.fancybox-nav {
      position: fixed;
      span{
        width: 60px;
        height: 110px;
        margin-top: -55px;
        background: transparent !important;
        &:before{
          content: '';
        }
      }
    }
    &.fancybox-next{
      right: 10px;
      span:before{
        font-size: 30px;
        @include sprite(fancyNext);
      }
    }
    &.fancybox-prev{
      left: 10px;
      span:before{
        font-size: 30px;
        @include sprite(fancyPrev);
      }
    }
    &.fancybox-close{
      top: 15px !important;
      right: 15px !important;
      width: 59px;
      height: 59px;
      background: transparent !important;
      position: fixed;
      &:before{
        content: '';
        @include sprite(fancyClose);
        font-size: 30px;
      }
    }
  }
}
#fancybox-thumbs{
  .outer{
    background-color: #fff;
    padding: 0 !important;
    .inner{
      padding: 10px 0 !important;
    }
    li.fancybox-thumb-active{
      padding: 3px;
      img{
        opacity: 1;
        border-bottom: solid 2px #000;
      }
    }
    ul{
      height: 80px !important;
      li{
        a{
          height: 80px !important;
          box-shadow: none;
          border: 0;
          border-radius: 0;
          background: transparent !important;
          img{
            width: 100% !important;
            height: auto !important;
            padding-bottom: 5px;
            opacity: .6;
            margin: 0 !important;
          }
        }
      }
    }
  }
}