body{
	h6{
		font-size: 20px;
		font-weight: 300;
		line-height: 25px;
		color: $DarkBG;
	}
	h5{
		font-size: 24px;
		font-weight: 300;
		line-height: 25px;
		color: $DarkBG;
	}
	h4{
		font-size: 26px;
		font-weight: 300;
		line-height: 25px;
		color: $DarkBG;
	}
	p,a,span{
		font-size: 14px;
		font-weight: 300;
		color: $DarkBG;
	}
	.header p{
		font-size: 18px;
		line-height: 26px;
	}
	a{
		text-decoration: none;
		&:hover{
			text-decoration: underline;
		}
	}
	.pink-link--color{
		span{
			color: $PinkTypecolor;
		}
		a:hover{
			color: $PinkTypecolor;
		}
	}
}