.main.main-good{
  padding-bottom: 0;
  .block-wrapp--good{
    margin-right: 290px;
    border-left: 0;
  }
  .block-good{
    padding-bottom: 20px;
    @extend .default-border-bottom;
  }
  .block-specifications{
    border: none;
  }
  .block-goods{
    .block-goods--wrapp{
      width: 50%;
      @extend .default-border-top;
      margin-top: -1px;
    }
  }
  .block--review{
    .block-goods_title{
      margin-bottom: 15px;
      .statusBadge{
        display: inline-block;
        float: none;
      }
    }
  }
  .block--desription{
    margin-top: 30px;
    @extend .clearfix-extendet;
    &-title {
      font-size: 24px;
      font-weight: 200;
      margin-top: 25px;
    }
    &-list {
      font-size: 14px;
      margin-top: 25px;
      li {
        line-height: 24px;
        font-weight: 200;
        vertical-align: middle;
        i {
          display: inline-block;
          margin-right: 5px;
          &:before {
            vertical-align: middle;
          }
        }
      }
      &-title {
        margin-bottom: 10px;
      }
    }

    p{
      line-height: 24px;
    }
  }
  .block-good--video{
    border: none;
  }
  .block-table{
    @extend .default-border-top;
    background-color: $MainBG;
    padding-bottom: 140px;
    text-align: center;
    .tab-pane.block-goods{
      .block-goods--wrapp{
        width: 25%;
      }
    }
    .tab-pane{
      width: 100%;
      a{
        text-decoration: none;
      }
    }
    .nav-tabs{
      display: inline-block;
      margin-top: 30px;
      border: none;
    }
    .block-carousel{
      padding-top: 0;
    }
    .tab-content{
      text-align: left;
    }
  }
  .block-review--img{
    position: relative;
    // width: 100%;
    // float: left;
    .navigation{
      margin-top: 15px;

    }
  }
  .block-review--desription{
    // float: right;
    // width: 50%;
    // padding-left: 30px;
    padding-top: 50px;
    .review-right--good{
      float: none;
      @extend .clearfix-extendet;
    }
    h4{
      margin-bottom: 20px;
      line-height: 35px;
    }
    p{
      margin-bottom: 15px;
      font-weight: 400;
    }
    span{
      font-size: 12px;
    }
    .choose_color{
      margin-top: 15px;
      @extend .clearfix-extendet;
      li{
        @extend .default-boreder;
        width: 50px;
        height: 50px;
        margin-right: 5px;
        float: left;
        overflow: hidden;
        position: relative;
        img{
          @extend .center.aboslute;
          width: 100%;
          height: auto;
        }
      }
    }
  }
  .desription--list{
    .table-row{
      width: 100%;
      position: relative;
      @extend .clearfix-extendet;
      margin-top: 15px;
      margin-bottom: 15px;
    }
    .table{
      vertical-align: middle;
      font-size: 12px;
      display: inline-block;
      a{
        font-weight: 400;
        font-size: 12px;
      }
    }
    .name{
      background-color: #fff;
      padding-right: 15px;
      padding-bottom: 5px;
      z-index: 2;
      position: relative;
    }
    .descript{
      float: right;
      padding-top: 2px;
      background-color: #fff;
      z-index: 2;
      position: relative;
      padding-left: 15px;
    }
    .sepparate {
        z-index: 1;
        position: absolute;
        bottom: 5px;
        right: 0;
        width: 100%;
        border-bottom: dashed 1px #e5e5e5;
    }
  }
}
table.stats{
  margin-top: 10px;
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
  border-spacing: 2px;
  .cap{
    background-color: #fff !important;
    border: 0 !important;
    td{
      padding-bottom: 15px;
      font-size: 16px;
    }
  }
  tr{
    border: 1px solid $BorderColor;
    &:hover{
      background-color: lighten($BorderColor, 10%);
    }
  }
  td{
    padding: 5px 0px;
    font-weight: 300;
    font-size: 14px;
    span,
    a{
      font-weight: 300;
      font-size: 14px;
    }
  }
  .table-name{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 40%;
    span {
      padding: 0px 10px;
    }
  }
  .gray{
    td{
      background-color: lighten($BorderColor, 10%);
    }
  }
}