.arrow-down.arrow-title {
  @include sprite(arrowDown);
  margin-left: 10px;
  vertical-align: middle;
  font-size: 1em;
}
.toggle-mobile-click {
  text-align: center;
  cursor: pointer;
  margin-bottom: 10px;
  span {
    font-size: 21px;
  }
  &.active {
    .arrow-down {
      transform: rotate(180deg);
    }
  }
}
.inner-table{
  width: 100%;
  margin-bottom: 25px;
  .date-head{
    font-size: 16px;
    background: lighten($MainBG, 2%);
    td{
      padding: 25px 0;
      @extend .default-border-bottom;
      border-bottom: solid 1px #dddddd;
      line-height: 30px;
      vertical-align: middle;
      &.date-title{
        padding-left: 10px;
      }
    }
    input{
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      @extend .default-boreder;
      background: #ffffff;
      padding-left: 12px;
      font-size: 14px;
      display: block;
      height: 30px;
      max-width: 300px;
      clear: both;
    }
  }
  .date-title{
    width: 150px;
  }
}
.starred_off{
  @include sprite(star);
  font-size: .5em;
  &.table-icon {
    vertical-align: -2px;
  }
  
}
.table-icon{
  vertical-align: middle;
}
tbody .inner-table-tr {
  &.inner-table-white,
  &.inner-table-red,
  &.inner-table-green,
  &.inner-table-grey {
    td {
      padding: 10px 15px;
    }
    .action-shipment {
      // &:hover {
      //   opacity: .6;
      // }
      .message-user-login {
        border-bottom: none;
      }
    }
  }
}
.inner-table-link,
.inner-table-btn {
  margin-right: 10px;
  font-weight: 700;
  font-size: 16px;
  color: #333;
  background-color: #fff;
  border-radius: 3px;
  padding: 5px;
  box-shadow: 0 0 3px rgba(0,0,0,.2);
}
.inner-table-link:hover {
  background-color: #E6E6E6;
  text-decoration: none;
}
.inner-table-tr{
  font-size: 14px;
  line-height: 24px;
  @extend .default-border-bottom;
  &.inner-table-grey {
    background-color: #EEF2F4;
    td {
      background-color: #EEF2F4;
    }
  }
  &.inner-table-red {
    background: $redColor;;
    td {
      color: #fff;
      background: $redColor;;
      i,span {
        color: #fff;
      }
    }
  }
  &.inner-table-green {
    background: $colorGreen;;
    td {
      color: #fff;
      background: $colorGreen;;
      i,span {
        color: #fff;
      }
    }
  }
  &.inner-table-white {
    background: #fff;;
    td {
      background: #fff;;
    }
  }
  &:nth-child(2n){
    background: darken($MainBG, 2%);
  }
  &.count-summ{
    font-size: 24px;
    text-align: right;
    background: darken($MainBG, 15%);
    width: 100%;
    td{
      width: 100%;
      padding: 15px;
      i{
        font-size: 9px;
      }
    }
  }
  .message-table-order{
    min-width: 120px;
    .starred_on{
      margin-bottom: 2px;
      font-size: .5em;
    }
  }
  .message-table-action{
    span.table-icon{
      margin-right: 5px;
    }
  }
  td{
    vertical-align: middle;
    
    &:not(:last-child){
      padding: 25px 15px 25px 0;
    }
  }
  .message-user{
    position: relative;
    &-inner{
      @extend .default-border-top;
      @extend .default-border-bottom;
      position: absolute;
      left: 0;
      top: 20px;
      background: $MainBG;
      box-shadow: 0 5px 10px 0 rgba(0,0,0,.2);
      z-index: 100;
      ul{
        padding: 15px;
      }
      i.rouble-icon{
        font-size: 5px;
        margin-left: 5px;
      }
    }
    &-info{
      padding-top: 10px;
      @extend .default-border-top;
      background: darken($MainBG, 10%);
    }
    li{
      margin-bottom: 10px;
      line-height: 21px;
      font-weight: 600;
      span{
        font-weight: 300;
      }
    }
  }
}
.message-user-login{
  @extend .border-bottem-dash-color;
  padding-bottom: 2px;
  text-decoration: none;
  &:hover{
    border: none;
    text-decoration: none;
  }
}
.table-checkbox.checkbox{
  &.hide-checkbox{
    display: none;
  }
  input[type="checkbox"]{
   display: none;
    &:checked + label:before{
      top: 5px;
    }
  }
  label.bx-filter-param-label:before{
    top: 5px;
  }
}
.table-contnent{
  font-size: 16px;
  background: lighten($MainBG, 2%);
  .in-work{
    .color a{
      color: $colorGreen;
    }
    border-left: solid 4px $colorGreen;
  }
  .new{
    .color a{
      color: $redColor;
    }
    border-left: solid 4px $redColor;
  }
  .table-th{
    display: none;
  }
  thead{
    @extend .default-border-bottom;
    th{
      padding: 25px 0;
      text-align: left;
      &:first-child{
        padding-left: 10px;
      }
      &.hide{
        width: 50px;
      }
    }
  }
  .even{
    text-align: center;
    padding: 50px 0;
    font-size: 21px;
    color: $BorderColor;
    text-transform: uppercase;
  }
  .pagination{
    margin: 50px;
  }
}
.button-wrapper .button-inner-table{
  &[type="submit"]{
    float: right;
  }
  margin-bottom: 50px;
}
.button-inner-table{
  &.button{
    background: darken($MainBG, 80%);
  }
  &[type="button"]{
    background: darken($MainBG, 80%);
    padding-left: 15px;
    padding-right: 15px;
  }
  &[type='submit']{
    border-radius: 5px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.table-nav-tabs{
  margin-top: 10px;
  width: 100%;
  a{
    line-height: 20px;
    vertical-align: top;
    display: block;
  }
}
.personSelect.tableSelect{
  .select-box .select{
    width: 138px;
  }
  .button-inner-table{
    font-size: 14px;
    letter-spacing: .07em;
  }
  .tableSelect-title{
    text-align: center;
    width: 250px;
    span{
      font-weight: bold;
      text-transform: uppercase;
      text-align: center;
      margin-top: 12px;
      font-size: 14px;
      display: block;
      color: $BorderColor;
    }
  }
  td{
    padding: 5px 0;
    vertical-align: middle;
    border-top: 0;
  }
  td:last-child{
    padding: 5px 0;
  }
  .tableSelect-button td{
    padding-top: 5px;
  }
}
.order-table{
