}
.btn-group{
    position: relative;
    cursor: pointer;
    float: right;
    margin-left: 15px;
    .defaultBtn{
      padding-left: 15px;
      padding-right: 15px;
    }
    &:hover{
      .dropdown{
        text-align: left;
        right: 0;
        display: block;
        background-color: #fff;
        box-shadow: 0 5px 10px 0 rgba(0,0,0,.2);
        padding: 15px;
        position: absolute;
        z-index: 2000;
        @extend .default-boreder;
        li{
