.hover-dropDown-list.toggle-link{
  position: relative;
  padding: 10px 5px 10px 15px;
  margin-top: -20px;
  margin-bottom: 20px;
  margin-left: -15px;
  margin-right: -15px;
  @extend .default-border-bottom;
  background: #f2f2f2; /* Old browsers */
  background: -moz-linear-gradient(top, #f2f2f2 0%, #ffffff 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #f2f2f2 0%,#ffffff 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #f2f2f2 0%,#ffffff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2f2f2', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
  .hover-dropDown-list-isHide.toggle-content{
    display: none;
    position: absolute;
    padding: 5px 5px 5px 15px;
    line-height: 21px;
    width: 100%;
    height: auto;
    top: 37px;
    left: 0;
    background-color: #fff;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
  }
}
.hover-dropDown-list.toggle-link--active{
  .hover-dropDown-list-isHide.toggle-content{
    display: block;
  }
}

.section-right {
  .filtr-title {
    text-align: center;
  }
}

.btn-circle {
  width: 100px;
  height: 100px;
  text-align: center;
  margin: 10px auto 20px;
  position: relative;
  z-index: 20;
  line-height: 100px;
  vertical-align: middle;
  cursor: pointer;
  -webkit-transition: transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  &.open {
    z-index: 30;
  }
  .wrap-icon {
    position: relative;
    background-color: #fff;
    z-index: 2;
    border-radius: 50%;
    border: 1px solid $redColor;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 9px 1px rgba(0, 0, 0, 0.2); 
  }
  &:hover .wrap-icon {
    -webkit-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transform: scale(1.1);
  }
  .tooltip {
    display: none;
    position: absolute;
    background-color: #fff;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
    text-align: left;
    top: 0;
    width: 250px;
    left: -210px;
    padding: 10px 60px 10px 20px;
    line-height: 1.4;
    z-index: 1;
    // &:before {
    //   content: '';
    //   position: absolute;
    //   right: -5px;
    //   top: 48px;
    //   width: 0;
    //   height: 0;
    //   border-style: solid;
    //   border-width: 10px 0 10px 10px;
    //   border-color: transparent transparent transparent #fff;
    // }
    p {
      margin: 10px 0;
    }
    .price {
      font-size: 18px;
      font-weight: bold;
    }
  }
}

.icon-filtr {
  background: url(../img/filtr.svg) center no-repeat;
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 40px;
}
.icon-price {
  background: url(../img/price-tag.svg) center no-repeat;
  display: inline-block;
