@import "varibles/sprite.scss";
@import "varibles/color.scss";
@import "varibles/type.scss";
@import "varibles/varibles.scss";
@import "varibles/carousel.scss";
@import "varibles/cart.scss";
@import "varibles/filter.scss";
@import "varibles/good.scss";
@import "varibles/fancybox.scss";
@import "varibles/inner-style.scss";
@import '../../bower_components/sass-mq/_mq.scss';
@import 'fotorama.scss';
@import 'fotorama.scss';

$ieSprite: '.lt-ie9' !default;
$sprite: map-get($icons, sprite) !default;
$baseFontSize: 16px !default;

@import "varibles/mixins.scss";
*{
	box-sizing: border-box;
}
.hidden-lg{
	display: none;
}
.pull-left{
	float: left;
}
.catalog-content {
	.title a{
		font-weight: 600;
	}
	&.cabinet-content {
		width: 320px;
		left: -191px;
	}
	li a{
		font-size: 12px;
	}
	li {
		position: relative;
		&:hover {
			ul {
				display: block;
			}
		}
	}
	&_hover {
		position: absolute;
		left: -320px;
		top: 0;
		padding-left: 15px;
		padding-top: 15px;
		display: none;
		margin-top: 0;
		background-color: $DarkBG;
		z-index: 100;
		width: 320px;
		li {
			border-bottom: none;
		}
	}
}
.wrapper{
	font-family: 'Open Sans';
}
.top-bar{
	line-height: 14px;
	z-index: 2;
	.select{
		vertical-align: -6px;
		line-height: 14px;
		padding-right: 20px;
	}
	.menu-icon{
		height: 20px;
		margin-top: 7px;
		margin-right: 25px;
	}
	a{
		color: #fff;
	}
	a,span{
		vertical-align: -5px;
		line-height: 14px;
		padding-right: 5px;
	}
	&--menu{
		float: left;
		color: #fff;
		margin-right: 35px;
		cursor: pointer;
		&:hover {
			color: $redColorHover;
			.toggle-link:after{
				border-color: $redColorHover transparent transparent transparent;
			}
			.toggle-link--active:after{
				top: 3px;
				border-color: transparent transparent $redColorActive transparent;
			}
		}
		span {
			color: inherit;
			position: relative;
			&.toggle-link--active {
				color: $redColorActive;
				&:after{
					top: 3px;
					border-color: transparent transparent $redColorActive transparent;
				}
			}
			&:after {
				content: "";
				width: 0;
				height: 0;
				border: 5px solid transparent;
				border-color: #fff transparent transparent transparent;
				position: absolute;
				top: 8px;
				right: -10px;
			}
		}
	}
	&--menu_wrap{
		z-index: 1000;
		display: none;
		position: absolute;
		left: 0;
		width: 100%;
		top: 28px;
		span,
		a{
			padding-left: 15px;
			padding-top: 15px;
			padding-bottom: 15px;
			line-height: 10px;
			display: block;
			background: $DarkBG;
		}
	}
	&--logIn{
		width: 25px;
		a{
			display: inline-block;
			padding-right: 5px;
		}
		margin-top: 3px;
		cursor: pointer;
		float: right;
	}
}
.usr_menu ~ .wrapper .top-bar--menu_wrap {
	top: 44px;
}
.social-block{
	a{
		margin-left: 20px;
		font-size: 26px;
	}
}
.braedcrumbs{
	margin-bottom: 22px;
	color: $DarkBG;
	line-height: 25px;
	font-size: 12px;
	a{
		font-size: 12px;
		color: $DarkBG;
		white-space: nowrap;
	}
	.marker{
		color: $DarkBG;
		font-size: 25px;
		vertical-align: middle;
	}
	span{
		font-size: 12px;
		color: $BorderColor;
		white-space: nowrap;
	}
}
.inside{
	h5{
		margin-bottom: 10px;
		line-height: 40px;
	}
}
.logo-icon{
	float: left;
	margin-right: 25px;
	height: 28px;
	&:before{
		font-size: .37em;
	}
}
.logo-mobile{
	margin-top: 8px;
}
.amount{
	font-weight: 400;
	font-size: 16px;
	border-radius: 3px;
	margin-left: -1px;
	float: right;
	display: block;
	padding: 3px 5px;
}
.header{
	padding: 23px 20px 23px 60px;
	@extend .default-border-bottom;
	position: relative;
	&-cabinet{
		text-align: right;
		float: right;
		margin-top: 8px;
		margin-left: 15px;
		.toggle-content{
			right: 0;
			width: 100%;
			top: 46px;
			padding: 10px 15px;
			position: absolute;
			background-color: $DarkBG;
			z-index: 1000;
			text-align: left;
			a{
				color: #fff;
				line-height: 41px;
				font-size: 21px;
			}
		}
		.cabinet-hide{
			display: none;
		}
	}
	a, p, span{
		color: $DarkBG;
	}
	.logo{
		position: absolute;
		top: 11px;
		left: 50px;
		width: 400px;
		.logo-icon:before{
			font-size: 18px;
		}
		a{
			display: block;
			float: left;
			margin-right: 25px;
			text-align: center;
			&:hover{
				text-decoration: none;
			}
			span{
				font-weight: 600;
				font-size: 18px;
			}
		}
		p{
			margin-top: 20px;
			padding-top: 5px;
			padding-bottom: 20px;
			@extend .border-icons;
			color: #474344;
			float: left;
			width: 165px;
		}
	}
}
.search{
	position: absolute;
	top: 0;
	width: 100%;
	min-width: 320px;
	left: 0;
	background-color: $BlackBG;
	padding:  15px 20px 30px;
	z-index: 10;
	display: none;
	.toggle-search{
		color: #fff;
		padding-bottom: 15px;
		text-align: right;
		margin-right: 10px;
		cursor: pointer;
		&:hover{
			color: darken(#fff, 30%);
		}
	}
	.wrap{
		margin-right: 85px;
	}
	input{
		border-radius: 3px;
		height: 50px;
		width: 100%;
		background-color: darken($BlackBG, 5%);
		padding: 0 15px;
		font-size: 18px;
		font-weight: 300;
		color: lighten($BlackBG, 35%);
		border: none;
	}
}
.button-search{
	border-radius: 3px;
	margin-top: 49px;
	background-color: darken($BlackBG, 5%);
	padding: 10px 11px;
	font-size: 12px;
	margin-left: 20px;
	float: right;
	cursor: pointer;
	&:hover{
		background-color: darken($BlackBG, 8%);
		&:before{
			@include sprite(searchRedHover);
		}
		&:active{
			@include sprite(searchRedActive);
		}
	}
	&:before{
		@include sprite(searchRed);
	}
}
.nav-bar{
	float: right;
	margin-top: 5px;
	margin-left: 20px;
	&--item{
		position: relative;
		padding-right: 10px;
		display: block;
		float: left;
		&:hover .cabinet-content a {
			color: #fff;
		}
		&_single{
			span{
				margin-top: 3px;
			}
		}
		a,span{
			color: #fff;
			cursor: pointer;
		}
		&.active,
		&.nav-bar--item_link{
			a{
				cursor: pointer;
			}
		}
		i{
			display: inline-block;
			vertical-align: middle;
			width: 30px;
			text-align: center;
			&:before{
				font-size: 10px;
			}
		}
		.user-icon:before{
			@include sprite(userWhite);
		}
		.catalog-icon:before{
			@include sprite(catalogWhite);
		}
		.toggle-link--active {
			span {
				color: $redColorHover;
			}
			.menuSandvichWhite-icon:before {
				@include sprite(menuSandvichHover);
			}
		}
		&:hover{
			color: $redColorHover;
			a,span{
				color: $redColorHover;
				cursor: pointer;
			}
			.menuSandvichWhite-icon:before{
				@include sprite(menuSandvichHover);
			}
			.phone-icon:before{
				@include sprite(phoneRedHover);
			}
			.favorite-icon:before{
				@include sprite(heartRedHover);
			}
			.cart-icon:before{
				@include sprite(cartRedHover);
			}
			.user-icon:before{
				@include sprite(userRedHover);
			}
			.catalog-icon:before{
				@include sprite(catalogRedHover);
			}
			.cartWhite-icon:before{
				@include sprite(cartRedHover);
			}
		}
		&:active{
			a,span{
				color: $redColorActive;
				cursor: pointer;
			}
			.menuSandvichWhite-icon:before{
				@include sprite(menuSandvichActive);
			}
			.phone-icon:before{
				@include sprite(phoneRedActive);
			}
			.favorite-icon:before{
				@include sprite(heartRedActive);
			}
			.cart-icon:before{
				@include sprite(cartRedActive);
			}
			.user-icon:before{
				@include sprite(userRedActive);
			}
			.catalog-icon:before{
				@include sprite(catalogRedActive);
			}
			.cartWhite-icon:before{
				@include sprite(cartRedActive);
			}
		}
		&__wrapper{
			float: right;
		}
		a{
			position: relative;
			display: block;
			&:hover{
				text-decoration: none;
			}
			&:after{
				content: " ";
				display: inline-block;
				vertical-align: middle;
				height: 100%;
			}
			span{
				display: block;
				&.amount{
					margin-top: 0;
					background-color: $BorderColor;
					color: $DarkBG;
				}
			}
		}
	}
}

//MAIN
.action_content {
	// margin: 15px 0 30px;
	a {
		display: block;
		margin-bottom: 10px;
		font-size: 18px;
		text-decoration: underline;
	}
}
.main{
	padding-top: 30px;
	padding-bottom: 60px;
	background-color: $MainBG;
	&.inside{
		background-color: #fff;
	}
	.left-side{
		background-color: #fff;
		padding: 11px 20px 0;
		margin-bottom: 30px;
		@extend .default-boreder;
		&--catalog {
			padding: 11px 0 0;
			.left-side_title {
				margin: -11px 0 0;
			}
			&_list li {
				padding: 0 20px;
				position: relative;
				line-height: 44px;
			}
		}
		&_title{
			padding: 11px 20px 15px;
			margin: -11px -20px 0;
			background-color: $DarkBG;
			h6{
				color: #fff;
			}
			.catalog-icon{
				margin-top: 2px;
				font-size: 10px;
			}
		}
		&--wrapp{
			padding: 15px 0;
		}
		&_img{
			text-align: center;
			width: 40%;
			height: 110px;
			float: left;
			@extend .default-boreder;
			overflow: hidden;
			position: relative;
			img{
				max-width: 150px;
				height: auto;
				@extend .center.aboslute;
			}
		}
		&_content{
			float: right;
			width: 60%;
			padding-left: 10px;
			a{
				margin-bottom: 15px;
				display: block;
				text-decoration: underline;
				&:hover{
					text-decoration: none;
				}
			}
		}
	}
	.left-side--catalog{
		h6{
			float: left;
		}
		i{
			float: right;
		}
		&_content{
			padding: 0 0 7px;
		}
		&_list{
			// margin-right: -25px;
			li{
				// padding-right: 25px;
				position: relative;
				line-height: 44px;
				&:hover{
					ul.hide{
						display: block;
					}
				}
			}
		}
		&_sub{
			margin-top: 18px;
			li{
				line-height: 20px;
			}
		}
		ul.hide{
			display: none;
			left: 269px;
			top: 0;
			width: 340px;
			background-color: #fff;
			z-index: 1000;
			position: absolute;
			box-shadow: 0 0 10px rgba(0,0,0,.4);
			li{
				font-size: 12px;
				line-height: 28px;
				> span{
					float: right;
					font-size: 12px;
				}
				&.title{
					font-size: 14px;
					font-weight: 600;
				}
			}
			.left-side--catalog_slider{
				@extend .default-border--left;
				background-color: #fff;
				position: absolute;
				right: -340px;
				z-index: 10000;
				width: 340px;
				top: 0;
				padding: 0;
				.jcarousel{
					max-height: 360px;
					height: 360px;
					li{
						height: 360px;
						padding: 0;
						img {
							min-width: auto;
						}
					}
				}
				top: 0;
				.jcarousel-wrapper{
					position: relative;
					margin-left: 0;
					margin-right: 0;
					left: 0 !important;
					width: 340px;
					.jcarousel-control{
						top: 100px;
						transform: translateY(0);
					}
					.block-goods_img{
						margin: 25px 0 0;
						overflow: visible;
					}
					.block-goods--wrapp{
						float: none;
						width: 340px;
						padding-bottom: 0;
						.block-goods--inCart{
							padding-top: 0;
						}
						.block-goods_hover{
							opacity: 1;
							z-index: 10010;
							padding: 15px;
						}
					}
					.defaultBtn{
						line-height: 18px;
					}
					.auxiliary-buttons{
						margin-top: 5px;
					}
				}
			}
		}
	}
	.left-side--news{
		font-size: 12px;
		.left-side--wrapp {
			padding-top: 30px;
		}
		a{
			text-decoration: underline;
			&:hover{
				text-decoration: none;
			}
		}
		&_more{
			margin: 30px -21px -16px;
			li{
				margin-bottom: -1px;
				padding-left: 25px;
				padding-right: 25px;
				@extend .default-boreder;
				a{
					font-weight: 300;
					font-size: 12px;
					line-height: 48px;
				}
				background: $InputBG;
			}
		}
	}
	.left-side-subscrible{
		text-align: center;
		form{
			margin-top: 25px;
		}
		input{
			padding: 5px 20px;
			font-size: 16px;
			width: 100%;
			margin-bottom: 20px;
		}
	}
	.left-side-bunner{
		overflow: hidden;
		padding: 0;
		img{
			width: auto;
			height: 100%;
		}
	}
	.aside-nav{
		left: 0;
		width: 270px;
		height: 100%;
		position: absolute;
		float: none;
		background-color: $BorderColor;
	}

	.left-side--nav{
		background-color: $BorderColor;
		width: 270px;
		margin-bottom: 0;
		padding-right: 0;
		&_content{
			text-align: right;
			li{
				margin-bottom: 10px;
				margin-right: -1px;
				vertical-align: middle;
				line-height: 42px;
				vertical-align: middle;
				&.active,
				&:hover{
					a{
						background-color: #fff;
					}
				}
				a{
					padding-right: 20px;
					padding-left: 15px;
					display: inline-block;
					position: relative;
					font-weight: 400;
					color: $redColor;
					&:hover{
						text-decoration: none;
					}
				}
				i{
					font-size: 12px;
					display: inline-block;
					width: 60px;
					text-align: center;
					&:before{
						display: inline-block;
						vertical-align: middle;
					}
				}
			}
		}
	}
	.section-right--good{
		padding: 0 15px;
		background-color: $BorderColor;
		position: absolute;
		height: 100%;
		right: 0;
		float: none;
		&.section-right--filtr {
			background-color: transparent;
			width: 130px;
			.fix-aside {
				width: 100px;
			}
		}
	}
	.review-right--good{
		display: none;
		.section-right--good_title .price{
			font-size: 31px;
		}
		.col{
			float: left;
			width: 50%;
		}
	}
	.section-right--good,
	.review-right--good{
		.defaultBtn{
			width: 100%;
			margin: 25px 0;
			padding: 16px 0 20px;
			span{
				margin-left: 25px;
				position: relative;
				color: #fff;
			}
			i{
				position: absolute;
				font-size: 8px;
				font-size: 11px;
				top: -2px;
				left: -25px;
			}
		}
		.retings{
			margin: 25px 0;
			float: none;
		}
		.auxiliary-buttons{
			margin-top: 0;
			height: 40px;
			width: 100%;
			float: none;
			a{
				margin-left: 5px;
				padding-left: 25px;
			}
			span{
				margin-left: 5px;
				padding-left: 5px;
				line-height: 16px;
				padding-bottom: 10px;
				vertical-align: top;
			}
			i{
				display: inline-block;
				width: auto;
				font-size: 10px;
				&.order-icon{
					line-height: 10px;
					&:before{
						font-size: 10px;
					}
				}
			}
		}
	}
	.section-right--good_title{
		margin-top: 15px;
		line-height: 42px;
		font-size: 12px;
		color: $GreyBG;
		.price{
			font-size: 18px;
			font-weight: 600;
		}
	}
	.section-right--banner{
		&:first-child{
			margin-bottom: 15px;
		}
		max-height: 162px;
		overflow: hidden;
	}
}
.section-content{
	width: 100%;
	float: left;
	position: relative;
}
// center main
.block{
	padding-top: 30px;
	clear: both;
	&-nav{
		color: #fff;
		li{
			float: left;
			width: 20%;
			text-align: center;
		}
		span {
			background: $DarkBG;
		}
		a{
			padding: 13px 15px 16px;
			font-size: 20px;
			line-height: 20px;
			display: block;
			color: #fff;
		}
		a{
			background: $DarkBG;
			&:hover,
			&.active{
				text-decoration: none;
			}
		}
		&-title{
			background: $DarkBG;
			width: 100%;
			span{
				padding-left: 15px;
				padding-right: 15px;
				display: inline-block;
			}
		}
	}
	&-goods,
	&-brands{
		@extend .clearfix-extendet;
	}
	&-goods_hover{
		transition: opacity .4s ease-in-out;
		opacity: 0;
		z-index: -1;
	}
	&-goods--inCart{
		position: absolute;
		width: 100%;
		top: 100%;
		left: 0;
		background-color: #fff;
		padding: 15px;
		box-shadow: 0 10px 9px -5px rgba(0,0,0,.2);
		@extend .default-border-bottom;
		.button{
			span{
				font-size: 21px;
				position: relative;
				color: #fff;
				display: inline-block;
				i{
					position: absolute;
					font-size: 8px;
					top: 0;
					left: -25px;
				}
			}
		}
	}
	&-goods--wrapp,
	&-brands--wrapp{
		background-color: #fff;
		float: left;
		position: relative;
		@extend .default-border-right;
		@extend .default-border-bottom;
		&:hover{
			.block-goods_hover{
				z-index: 10;
				opacity: 1;
			}
		}
		p{
			line-height: 21px;
			padding: 5px 15px;
			&.price{
				font-weight: 600;
				font-size: 18px;
				height: 45px;
				span{
					font-weight: 300;
					font-size: 12px;
					text-decoration: line-through;
				}
			}
		}
		.block-goods_description {
			p {
				height: auto;
				max-height: 52px;
			}
		}
		.filter_select {
			border-top: solid 1px #ABABAB;
			background-color: rgba(255, 255, 255, .8);
			padding: 4px 15px;
			li span {
				float: right;
			}
		}
		&_more{
			background: transparent;
			border: none;
			position: relative;
			height: 320px;
			text-align: center;
			padding: 0;
			.center{
				cursor: pointer;
			}
			p{
				font-size: 18px;
				padding: 0;
				background-color: transparent;
				height: auto;
				&.sum{
